.section-inhaler {
  height: 100vh;
  padding: 5vh 0;
  background-color: $brokenwhite;
  overflow: hidden;

  h1, h2, h3, h4, h5, h6, p {
    color: $red;
  }

  p {
    margin-top: 10px;
  }

  .letter {
    position: relative;
    display: inline-block;
  }

  .body {
    display: block;
  }

  .container {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 5vw;

    .flex {
    padding: 10vh 0;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

      .text-block {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
      }

      .animation {
        width: 400px;
        height: 400px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .text-block-2 {
          width: 100%;
          opacity: 0;

          .chance {
            display: flex;
            align-items: flex-start;

            h2 {
              background-color: $black;
              padding: 0 10px 3px 10px;
              margin-left: 10px;
            }

            p {
              font-weight: 900;
              transform: translateY(4px);
            }
          }

          .explain-text {
            max-width: 400px;
            margin-right: 0;
          }
        }

        .inhaler-container {
          position: absolute;
          top: 0;
          z-index: 99;

          .inhaler-top {
            position: absolute;
            content: '';
            left: 0;
            top: 0;
            width: 100px;
          }

          .inhaler {
            position: absolute;
            content: '';
            left: 11px;
            top: 40px;
            width: 165px;
          }
        }

        .spray {
          width: 600px;
          position: absolute;
          content: '';
          transform: rotate(-20deg);
          left: -240px;
          bottom: -20px;
        }

        .spray-container {
          position: absolute;
          content: "";
          transform: rotate(-20deg);
          right: 10px;
          bottom: -200px;
          width: 630px;
          height: 700px;
          overflow: hidden;
        }

        .spray-reveal {
          position: absolute;
          content: '';
          width: 100%;
          height: 100%;
          background: $brokenwhite;
        }
      }
    }
  }
}

.practice {
  height: 200vh;
}
